.members {
  width: 1110px;
  height: 717px;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-top: 156px;
}
.head_members {
  width: 635px;
  height: 55px;
  display: flex;
  justify-content: flex-start;
